import axios from '@/axios.js'

var api = {
    // 注册
    singUp(params) {
        return axios.post('/api/user/register', params);
    },
    // 发送验证码
    sendCode(params) {
      return axios.post('/api/user/sendMobileCode', params)
    },
    // 登录
    singIn(params) {
      return axios.post('/api/user/login', params)
    },
    // 表单信息
    tableInfo() {
      return axios.get('/api/form/getInitData')
    }
}

export default api;